export default {
    data () {
        return {
            model: false,
            vo: {
                id: null
            },
            loadingBtn: false,
            addTitle: '新增',
            updateTitle: '修改'
        };
    },
    computed: {
        isUpdate () {
            return this.vo.id !== null && this.vo.id !== undefined;
        },
        postUrl () {
            return this.isUpdate ? this.updateUrl : this.addUrl;
        },
        modelTitle () {
            return this.isUpdate ? this.updateTitle : this.addTitle;
        },
        generateAddOrUpdate () {
            return { ...this.vo };
        },
        formRef () {
            return 'form';
        }
    },
    methods: {
        generateRemove (item) {
            return {};
        },
        async beforeAddOrUpdate () {},
        async afterAddOrUpdate () {},
        async doRequest () {
            return this.post(this.postUrl, this.generateAddOrUpdate);
        },
        async addOrUpdate () {
            const form = this.$refs[this.formRef];
            (Array.isArray(form) ? form[0] : form).validate(async (valid) => {
                if (valid) {
                    await this.doAddOrUpdate();
                } else {
                    this.resetLoadingBtn();
                    this.$Message.error('表单验证失败!');
                }
            });
        },
        async doAddOrUpdate () {
            const before = await this.beforeAddOrUpdate();
            let success = true;
            let res;
            if (before !== false) {
                res = await this.doRequest();
                ({ success } = res);
            }
            if (success === false) {
                this.resetLoadingBtn();
            } else {
                this.model = false;
                this._timeoutQuery();
            }
            await this.afterAddOrUpdate(success, res);
            return success;
        },
        async beforeRemove (item) {
            if (this.__removeUrl) {
                this.removeUrl = this.__removeUrl;
            } else {
                this.__removeUrl = this.removeUrl;
            }
            this.removeUrl = `${this.removeUrl}/${item.id}`;
        },
        async afterRemove (success, item) {},
        async remove (item) {
            if (!item) return;
            const before = await this.beforeRemove(item);
            let success = true;
            if (before !== false) {
                const res = await this.post(this.removeUrl, this.generateRemove(item));
                ({ success } = res);
            }
            if (success === false) {
                this.resetLoadingBtn();
            } else {
                this._timeoutQuery();
            }
            await this.afterRemove(success, item);
            return success;
        },
        add () {
            this.model = true;
            this.vScrollToTop();
            this.loadingBtn = true;
            this.$refs[this.formRef].resetFields();
            this.vo.id = null;
        },
        update (item) {
            this.model = true;
            this.vScrollToTop();
            this.loadingBtn = true;
            Object.keys(this.vo).filter(key => Reflect.has(item, key)).forEach(key => this.vo[key] = item[key]);
        },
        vScrollToTop (ref = 'vScroll') {
            if (this.$refs[ref]) {
                this.$refs[ref].scrollTo({ x: 0, y: 0 });
            }
        },
        cancel () {
            this.loadingBtn = false;
        },
        resetLoadingBtn () {
            this.loadingBtn = false;
            this.$nextTick(() => this.loadingBtn = true);
        },
        _timeoutQuery () {
            if (typeof this.doQuery === 'function') {
                setTimeout(() => this.doQuery(), 500);
            }
        }
    }
};
