<template>
    <div>
        <Tabs v-model="currentTab">
            <TabPane v-for="item in list" :key="item.type" :name="`tab-${item.type}`" :label="item.name">
                <Card style="width: 600px">
                    <div v-if="!isEdit">
                        <CellGroup v-if="item.type === 1">
                            <Cell title="应用ID" :extra="item.appId"/>
                            <Cell title="应用秘钥" :extra="item.appSecret"/>
                            <Cell title="商户号" :extra="item.mchId"/>
                            <Cell title="商户密钥" :extra="item.mchKey"/>
                        </CellGroup>
                        <CellGroup v-if="item.type === 2">
                            <Cell title="商户号" :extra="item.partner"/>
                            <Cell title="商户秘钥" :extra="item.key"/>
                            <Cell title="卖家邮箱" :extra="item.sellerEmail"/>
                            <Cell title="应用ID" :extra="item.appId"/>
                            <Cell title="应用秘钥" :extra="item.privateKey"/>
                            <Cell title="支付宝应用公钥" :extra="item.alipayPublicKey"/>
                        </CellGroup>
                        <Row style="text-align: center">
                            <i-switch size="large" :value="item.status" @on-change="v => status(item, v)" :loading="loadingBtn" style="margin-right: 20px">
                                <span slot="open">启用</span>
                                <span slot="close">禁用</span>
                            </i-switch>
                            <Button type="primary" style="margin-right: 20px" @click="edit(item)">{{item.id ? '修改' : '新增'}}</Button>
                            <Poptip confirm transfer title="确定删除该收款配置吗?" @on-ok="remove(item)">
                                <Button type="dashed">删除</Button>
                            </Poptip>
                        </Row>
                    </div>
                    <Form :ref="`form-${item.type}`" :model="modelVo" :label-width="80" :rules="validate" v-else>
                        <template v-if="vo.type === 1">
                            <Form-item label="应用ID" prop="wx.appId">
                                <Input v-model.trim="config.wx.appId" />
                            </Form-item>
                            <Form-item label="应用秘钥" prop="wx.appSecret">
                                <Input v-model.trim="config.wx.appSecret" />
                            </Form-item>
                            <Form-item label="商户号" prop="wx.mchId">
                                <Input v-model.trim="config.wx.mchId" />
                            </Form-item>
                            <Form-item label="商户密钥" prop="wx.mchKey">
                                <Input v-model.trim="config.wx.mchKey" />
                            </Form-item>
                            <Form-item label="证书P12" prop="cert">
                                <div style="display: flex; flex-direction: row; justify-content: space-between;">
                                    <Upload :before-upload="handleCert" action="">
                                        <Button icon="ios-cloud-upload-outline">选择文件</Button>
                                    </Upload>
                                    <div v-if="!!cert">{{cert.name}}</div>
                                </div>
                            </Form-item>
                        </template>
                        <template v-else-if="vo.type === 2">
                            <Form-item label="商户号" prop="alipay.partner">
                                <Input v-model.trim="config.alipay.partner" />
                            </Form-item>
                            <Form-item label="商户秘钥" prop="alipay.key">
                                <Input v-model.trim="config.alipay.key" />
                            </Form-item>
                            <Form-item label="卖家邮箱" prop="alipay.sellerEmail">
                                <Input v-model.trim="config.alipay.sellerEmail" />
                            </Form-item>
                            <Form-item label="应用ID" prop="alipay.appId">
                                <Input v-model.trim="config.alipay.appId" />
                            </Form-item>
                            <Form-item label="应用秘钥" prop="alipay.privateKey">
                                <Input v-model.trim="config.alipay.privateKey" />
                            </Form-item>
                            <Form-item label="应用公钥" prop="alipay.alipayPublicKey">
                                <Input v-model.trim="config.alipay.alipayPublicKey" />
                            </Form-item>
                        </template>
                        <Row style="text-align: center">
                            <Button type="primary" :loading="loadingBtn" style="margin-right: 20px" @click="addOrUpdate">保存</Button>
                            <Button type="dashed" :loading="loadingBtn" @click="cancel">取消</Button>
                        </Row>
                    </Form>
                </Card>
            </TabPane>
        </Tabs>
    </div>
</template>

<script>
import { PaymentConfigType } from '@/libs/dic';
import ModelView from '@/mixins/model-view';

export default {
    name: 'PaymentConfig',
    mixins: [ ModelView ],
    data () {
        return {
            PaymentConfigType,
            list: [],
            vo: {
                type: null,
                json: null,
                cert: null
            },
            currentTab: null,
            isEdit: false,
            cert: null,
            config: {
                wx: {
                    appId: null,
                    mchId: null,
                    mchKey: null,
                    appSecret: null
                },
                alipay: {
                    partner: null,
                    key: null,
                    sellerEmail: null,
                    appId: null,
                    privateKey: null,
                    alipayPublicKey: null
                }
            },
            validate: {
                'wx.appId': [{ required: true, trigger: 'blur', message: '请填写应用ID' }],
                'wx.appSecret': [{ required: true, trigger: 'blur', message: '请填写应用秘钥' }],
                'wx.mchId': [{ required: true, trigger: 'blur', message: '请填写商户号' }],
                'wx.mchKey': [{ required: true, trigger: 'blur', message: '请填写商户密钥' }],
                'wx.cert': [{ required: true, trigger: 'change', message: '请选择微信证书P12文件' }],
                'alipay.partner': [{ required: true, trigger: 'blur', message: '请填写商户号' }],
                'alipay.key': [{ required: true, trigger: 'blur', message: '请填写商户密钥' }],
                'alipay.sellerEmail': [{ required: true, type: 'email', trigger: 'blur', message: '请填写卖家邮箱' }],
                'alipay.appId': [{ required: true, trigger: 'blur', message: '请填写应用ID' }],
                'alipay.privateKey': [{ required: true, trigger: 'blur', message: '请填写应用秘钥' }],
                'alipay.alipayPublicKey': [{ required: true, trigger: 'blur', message: '请填写应用公钥' }]
            },
            addUrl: '/api/ep-payment-config/add',
            updateUrl: '/api/ep-payment-config/update',
            removeUrl: '/api/ep-payment-config/remove'
        };
    },
    computed: {
        formRef () {
            return `form-${this.vo.type}`;
        },
        modelVo () {
            return { ...this.vo, ...this.config };
        },
        generateAddOrUpdate () {
            return { ...this.vo, json: JSON.stringify(this.vo.type === 1 ? this.config.wx : this.config.alipay) };
        }
    },
    async created () {
        await this.doQuery();
    },
    methods: {
        async doQuery () {
            const result = await this.get('/api/ep-payment-config/list');
            const data = result.success ? result.value || [] : [];
            this.list = PaymentConfigType.map(c => {
                const item = data.find(item => item.type === c.id);
                return {
                    ...JSON.parse(item?.json || '{}'),
                    ...(item || {}),
                    type: c.id,
                    name: c.name
                };
            });
            this.isEdit = false;
            this.resetLoadingBtn();
        },
        edit (item) {
            item.id && this.update(item);
            if (item.json) {
                this.config[item.type === 1 ? 'wx' : 'alipay'] = JSON.parse(item.json);
            }
            this.resetLoadingBtn();
            this.vo.type = item.type;
            this.isEdit = true;
        },
        cancel () {
            this.$super(ModelView).cancel();
            this.isEdit = false;
        },
        addOrUpdate () {
            this.loadingBtn = true;
            this.$super(ModelView).addOrUpdate();
        },
        status (item, status) {
            this.update(item);
            if (item.json) {
                this.config[item.type === 1 ? 'wx' : 'alipay'] = JSON.parse(item.json);
            }
            this.vo.status = status;
            this.doAddOrUpdate();
        },
        handleCert (file) {
            const fr = new FileReader();
            fr.onload = e => this.vo.cert = e.target.result.replace(/data:.*;base64,/, '');
            fr.readAsDataURL(file);
            this.cert = file;
            return false;
        },
        resetLoadingBtn () {
            this.loadingBtn = false;
        }
    }
};
</script>

<style scoped>

</style>
